body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset,
img,
abbr {
  border: 0;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption,
th {
  text-align: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

.wrapper {
  max-width: 960px;
  padding-left: 30px;
  padding-right: 30px;
  margin: 0 auto;
}

body {
  overflow-x: hidden;
}

.open-light {
  font-family: 'Open Sans', sans-serif;
  font-weight: 300;
}

.open-regular {
  font-family: 'Open Sans', sans-serif;
  font-weight: 400;
}

.open-semi-bold {
  font-family: 'Open Sans', sans-serif;
  font-weight: 600;
}

.raleway-semi-bold {
  font-family: 'Raleway', sans-serif;
  font-weight: 600;
}

.raleway-medium {
  font-family: 'Raleway', sans-serif;
  font-weight: 500;
}

.raleway-bold {
  font-family: 'Raleway', sans-serif;
  font-weight: 700;
}

.header {
  left: 0;
  background-color: #24282f;
}

.header__navigation {
  display: flex;
  margin-left: -15px;
  margin-right: -15px;
  padding-top: 40px;
  padding-bottom: 40px;
  flex-wrap: wrap;
  justify-content: space-between;
}

.header__logo {
  box-sizing: border-box;
  margin-left: 15px;
  margin-right: 15px;
  word-wrap: break-word;
}

.header__logo h3 {
  display: inline-block;
  text-transform: uppercase;
  color: #fff;
  font-size: 26px;
  margin-left: 20px;
  vertical-align: 4px;
}

.header__nav {
  box-sizing: border-box;
  margin-left: 15px;
  margin-right: 15px;
  word-wrap: break-word;
}

.header__nav ul > li {
  display: inline-block;
  vertical-align: -6px;
  text-transform: uppercase;
  list-style-type: none;
  margin-left: 65px;
  cursor: pointer;
}

.header__nav ul > li:first-child {
  margin-left: 0;
}

.header__nav a {
  font-family: 'Open Sans', sans-serif;
  font-weight: 400;
  text-decoration: none;
  font-size: 14px;
  color: #fff;
}

.header__nav a:hover {
  padding-bottom: 44px;
  border-bottom: 2px solid #b5a263;
  transition-delay: 0.2s;
}

.menu {
  display: none;
}

.about {
  min-height: 384px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  position: relative;
  z-index: 5;
  background-image: url(../img/bgc-one.jpg);
}

.about__wrap {
  position: absolute;
  z-index: -1;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: #2b2e35;
  background-position: center;
  opacity: 0.93;
  width: 100%;
  height: 100%;
}

.about__text {
  padding-top: 88px;
}

.about__text h1 {
  color: #fff;
  font-size: 35px;
  font-family: 'Raleway', sans-serif;
  font-weight: 700;
  text-transform: uppercase;
}

.service {
  justify-content: space-between;
  background-color: #fff;
}

.service .row {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
}

.service__txt {
  padding-top: 155px;
  padding-bottom: 33px;
}

.service__txt h2 {
  text-align: center;
  color: #24282f;
  font-size: 23.98px;
  font-family: 'Raleway', sans-serif;
  font-weight: 600;
  text-transform: uppercase;
}

.service__txt span {
  display: block;
  height: 2px;
  width: 78px;
  background-color: #b5a263;
  margin: 27px auto;
}

.service__workers {
  box-sizing: border-box;
  margin-left: 15px;
  margin-right: 15px;
  word-wrap: break-word;
  width: calc(33.333333333333336% - 30px);
}

.service__workers img {
  width: 311px;
  height: 300px;
}

.service__more {
  margin-top: 25px;
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  justify-content: space-between;
}

.service__more .service_info {
  box-sizing: border-box;
  margin-left: 15px;
  margin-right: 15px;
  word-wrap: break-word;
}

.service__more .service_info h3 {
  font-family: 'Raleway', sans-serif;
  font-weight: 500;
  font-size: 16px;
  text-transform: uppercase;
  color: #232323;
}

.service__more .service_info span {
  font-size: 14px;
  font-family: 'Open Sans', sans-serif;
  font-weight: 400;
  color: #b5a263;
}

.service__more .service__links {
  box-sizing: border-box;
  margin-left: 15px;
  margin-right: 15px;
  word-wrap: break-word;
}

.service__more .service__links a {
  display: inline-block;
  width: 34px;
  height: 34px;
  background-color: #dedede;
  border-radius: 50%;
  position: relative;
}

.service__more .service__links a:hover {
  background-color: #c5c5c5;
}

.service__more .service__links i {
  top: 8px;
  left: 10px;
  position: absolute;
  font-size: 16px;
  font-weight: 400;
  color: #fff;
}

.service__all {
  box-sizing: border-box;
  margin-left: 15px;
  margin-right: 15px;
  word-wrap: break-word;
  margin: 42px auto;
}

.service__all .btn {
  border-width: 1px;
  border-color: #24282f;
  color: #24282f;
  font-family: 'Open Sans', sans-serif;
  font-weight: 400;
  font-size: 16px;
  text-transform: uppercase;
  outline: none;
  background-color: #fff;
  padding: 10px 30px;
  border-radius: 50px;
  cursor: pointer;
}

.service__all .btn:hover {
  background-color: #dedede;
}

.portfolio {
  background-color: #f7f7f7;
}

.portfolio .row {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
}

.portfolio__txt {
  box-sizing: border-box;
  margin-left: 15px;
  margin-right: 15px;
  word-wrap: break-word;
  margin: 0 auto;
  margin-top: 115px;
}

.portfolio__txt h2 {
  text-align: center;
  color: #24282f;
  font-size: 24px;
  font-family: 'Raleway', sans-serif;
  font-weight: 600;
  text-transform: uppercase;
}

.portfolio__txt span {
  display: block;
  height: 2px;
  width: 78px;
  background-color: #24282f;
  margin: 27px auto;
}

.portfolio__slider {
  position: relative;
  margin-top: 55px;
  box-sizing: border-box;
  margin-left: 15px;
  margin-right: 15px;
  word-wrap: break-word;
  width: calc(100% - 30px);
  text-align: center;
}

.portfolio__slider .item {
  width: calc(83.33333333333334% - 30px);
  margin-left: calc(8.333333333333334% + 15px);
  margin-right: calc(8.333333333333334% + 15px);
}

.portfolio__slider .item img {
  width: 90px;
  height: 90px;
  display: block;
  margin: 0 auto;
}

.portfolio__slider .item p {
  margin-top: 35px;
  line-height: 2;
  color: #000000;
  font-family: 'Open Sans', sans-serif;
  font-weight: 300;
  font-size: 14px;
}

.portfolio__slider .item h3 {
  margin-top: 40px;
  font-size: 16px;
  text-transform: uppercase;
  font-family: 'Open Sans', sans-serif;
  font-weight: 600;
  color: #b5a263;
}

.portfolio__slider .item strong {
  display: block;
  margin-top: 15px;
  font-family: 'Open Sans', sans-serif;
  font-weight: 600;
  font-size: 14px;
  color: #000000;
  margin-bottom: 115px;
}

.portfolio__slider .owl-nav {
  background-color: red;
  position: absolute;
  top: 0;
  width: 100%;
}

.portfolio__slider .owl-nav .owl-prev {
  display: block;
  position: absolute;
  left: 0;
}

.portfolio__slider .owl-nav .owl-next {
  display: block;
  position: absolute;
  right: 0;
}

.contact {
  background-color: #2b2f35;
}

.contact .row {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
}

.contact__info {
  margin-top: 70px;
  margin-bottom: 40px;
  box-sizing: border-box;
  margin-left: 15px;
  margin-right: 15px;
  word-wrap: break-word;
  width: calc(33.333333333333336% - 30px);
}

.contact__info h3 {
  font-family: 'Open Sans', sans-serif;
  font-weight: 600;
  font-size: 14px;
  color: #b5a263;
  text-transform: uppercase;
}

.contact__info p {
  margin-top: 20px;
  color: #6e737a;
  font-family: 'Open Sans', sans-serif;
  font-weight: 400;
  font-size: 14px;
}

.contact__info span {
  color: #6e737a;
  font-family: 'Open Sans', sans-serif;
  font-weight: 400;
  font-size: 14px;
}

.contact__info input {
  padding-left: 10px;
  color: #6e737a;
  margin-top: 15px;
  margin-bottom: 15px;
  background-color: #2b2f35;
  border: 2px solid #6e737a;
  outline: none;
  padding: 6px 10px;
}

.contact__info .contact__btn {
  display: block;
  text-transform: uppercase;
  font-family: 'Open Sans', sans-serif;
  font-weight: 400;
  font-size: 14px;
  background-color: #b5a263;
  color: #fff;
  border: none;
  padding: 6px 50px;
  outline: none;
}

.footer {
  background-color: #24282f;
}

.footer .row {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  justify-content: space-between;
  padding-top: 20px;
  padding-bottom: 20px;
}

.footer__info {
  box-sizing: border-box;
  margin-left: 15px;
  margin-right: 15px;
  word-wrap: break-word;
  width: calc(66.66666666666667% - 30px);
}

.footer__info p {
  margin-top: 10px;
  color: #6e737a;
  font-family: 'Open Sans', sans-serif;
  font-weight: 400;
  font-size: 12px;
}

.footer__link {
  box-sizing: border-box;
  margin-left: 15px;
  margin-right: 15px;
  word-wrap: break-word;
  width: calc(33.333333333333336% - 30px);
  text-align: center;
}

.footer__link a {
  margin-left: 5px;
  display: inline-block;
  width: 32px;
  height: 32px;
  background-color: #24282f;
  border-radius: 50%;
  border: 2px solid #6e737a;
  position: relative;
}

.footer__link a:first-child {
  margin-left: 0;
  margin-left: -27px;
}

.footer__link i {
  top: 8px;
  left: 10px;
  position: absolute;
  font-size: 16px;
  font-weight: 400;
  color: #6e737a;
}

@media (max-width: 1017px) {
  .service__links a {
    width: 24px !important;
    height: 24px !important;
  }

  .service__links i {
    top: 4px !important;
    left: 6px !important;
    font-size: 13px !important;
  }
}

@media screen and (max-width: 992px) {
  .wrapper {
    padding-left: 20px;
    padding-right: 20px;
  }

  .service__more {
    display: block;
  }

  .service__more .service_info {
    text-align: center;
    margin-bottom: 10px;
  }

  .service__more .service_info h3 {
    font-size: 14px;
  }

  .service__more .service_info span {
    font-size: 12px;
  }

  .service__more .service__links {
    text-align: center;
  }

  .service__more .service__links a {
    width: 24px;
    height: 24px;
  }

  .service__more .service__links i {
    top: 4px;
    left: 6px;
    font-size: 13px;
  }

  .contact__info input {
    font-size: 14px;
    padding: 6px 13px;
  }

  .contact__info .contact__btn {
    font-size: 12px;
    margin: 0 auto;
  }
}

@media (max-width: 760px) {
  .header__nav {
    display: none;
  }

  .menu-link {
    z-index: 899;
    width: 50px;
    height: 50px;
    display: block;
    background-color: #fff;
    border-radius: 50%;
    box-shadow: 1px 1px 10px rgba(0, 0, 0, 0.4);
    position: absolute;
    right: 20px;
    top: 30px;
  }

  .menu-link span,
  .menu-link span:before,
  .menu-link span:after {
    display: block;
    height: 2px;
    width: 18px;
    right: 50%;
    margin-right: -9px;
    background-color: #333;
    position: absolute;
    top: 50%;
    margin-top: -1px;
  }

  .menu-link span:before {
    transition: all 0.2s;
    content: '';
    margin-top: -6px;
  }

  .menu-link span:after {
    transition: all 0.2s;
    content: '';
    margin-top: 4px;
  }

  .menu-link_active span {
    background-color: transparent;
  }

  .menu-link_active span:before {
    margin-top: 0;
    transform: rotate(135deg);
  }

  .menu-link_active span:after {
    margin-top: 0;
    transform: rotate(-135deg);
  }

  .menu {
    opacity: 0.9;
    z-index: 800;
    height: 100vh;
    width: 100vw;
    position: absolute;
    top: 0;
    background-color: purple;
    padding: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    right: -100vw;
    transition: all 0.4s;
  }

  .menu-active {
    right: 0;
  }

  .menu ul {
    text-align: center;
    list-style: none;
  }

  .menu ul li {
    width: 100%;
  }

  .menu ul a {
    text-decoration: none;
    display: block;
    width: 110vw;
    font-weight: 100;
    letter-spacing: 4px;
    line-height: 4;
    font-family: 'Open Sans', sans-serif;
    font-weight: 400;
    color: #fff;
    transition: all 0.2s;
  }

  .menu ul a:hover {
    transition: all 0.2s;
    background-color: #b5a263;
  }

  .info__text h1 {
    font-size: 30px;
  }
}

@media screen and (max-width: 720px) {
  .wrapper {
    padding-left: 10px;
    padding-right: 10px;
  }

  .service__workers {
    width: calc(100% - 30px);
  }

  .service__workers {
    margin-bottom: 20px;
    text-align: center;
  }

  .service__workers img {
    width: 280px;
    height: 270px;
  }

  .portfolio__txt h2 {
    font-size: 20px;
  }

  .contact__info {
    width: calc(75% - 30px);
  }

  .contact__info {
    margin-bottom: 20px;
    margin-top: 20px;
    text-align: center;
    margin-left: calc(16.666666666666668% + 15px);
    margin-right: calc(16.666666666666668% + 15px);
  }

  .footer__info {
    width: calc(100% - 30px);
  }

  .footer__info {
    text-align: center;
    margin-bottom: 20px;
  }

  .footer__link {
    width: calc(100% - 30px);
  }

  .footer__link a:first-child {
    margin-left: 0;
  }
}

@media screen and (max-width: 576px) {
  .wrapper {
    padding-left: 5px;
    padding-right: 5px;
  }
}

@media screen and (max-width: 380px) {
  .wrapper {
    padding-left: 5px;
    padding-right: 5px;
  }
}